.popular{

  margin-top: 60px;

  &__content{
    margin-top: 63px;
  }

  &__list{
    max-width: 490px;
    column-gap: 27px;
    display: flex;
  }


  &__navigation{
    display: flex;
    justify-content: center;
    gap: 30px;
    margin-top: 65px;
  }

  &__swiper-pagination{
    position: relative;
    color: var(--white);
    font-size: 20px;
    font-style: var(--normal);
    font-weight: var(--fon500);
    line-height: var(--normal);
    max-width: 60px;
    max-height: 25px;
  }
}

@media (max-width: 1400px){

  .popular{

    &__menu{
      flex-direction: column;
      row-gap: 10px;
    }

    &__content{
      margin-top: 30px;
    }
  }


}


@media (max-width: 525px){

  .popular{
    margin-top: 30px;

    &__list{
      display: none;
    }

    &__title {
      display: flex;
      column-gap: 10px;
      align-items: center;
    }

    &__t2{
      position: relative;

      &::after{
        content: url(../img/svg/bur.svg);
        top: -2px;
        right: -24px;
        position: absolute;
      }
    }
  }

}


